.confirm__message {
  font-size: 1rem;
  color: #222;
  text-align: center;
}

.confirm__actions {
  margin: 2rem 0;
  display: flex;
  gap: 8px;
  justify-content: space-between;
}

.confirm__actions .btn-danger {
  background: #de2828;
  color: white;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.confirm__actions .btn-ghost {
  background: transparent;
  color: #374151;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.confirm__actions .btn {
  flex: 0 0 140px;
}

.confirm__warning {
  font-size: 0.9rem;
  color: #6b7280;
  margin-top: 6px;
  margin-bottom: 8px;
}
