.reset-password {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;

  &__container {
    width: 100%;
    max-width: 400px;
    background: white;
    border-radius: 12px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
    padding: 2rem;
  }

  &__header {
    text-align: center;
    margin-bottom: 32px;
  }

  &__logo {
    font-size: 28px;
    font-weight: 700;
    color: #667eea;
    margin: 0 0 12px 0;
  }

  &__subtitle {
    font-size: 14px;
    color: #666;
    margin: 0;
  }

  &__loading {
    text-align: center;
    padding: 40px 0;

    .spinner {
      width: 40px;
      height: 40px;
      margin: 0 auto 20px;
      border: 4px solid #f3f3f3;
      border-top: 4px solid #667eea;
      border-radius: 50%;
      animation: spin 1s linear infinite;
    }

    p {
      font-size: 14px;
      color: #666;
    }

    @keyframes spin {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
  }

  &__error {
    text-align: center;
    padding: 20px 0;

    .error-icon {
      width: 60px;
      height: 60px;
      margin: 0 auto 20px;
      background: #f8d7da;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #e74c3c;

      svg {
        width: 32px;
        height: 32px;
      }
    }

    h2 {
      font-size: 18px;
      font-weight: 600;
      color: #333;
      margin-bottom: 12px;
    }

    p {
      font-size: 14px;
      color: #666;
      margin-bottom: 24px;
      line-height: 1.5;
    }
  }

  &__no-token {
    text-align: center;
    padding: 18px 0 8px;

    .info-icon {
      width: 56px;
      height: 56px;
      margin: 0 auto 14px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #2563eb;
      background: #eef2ff;
      border-radius: 50%;
    }

    h2 {
      font-size: 18px;
      margin-bottom: 8px;
      color: #111827;
    }

    .muted {
      color: #6b7280;
      font-size: 13px;
      margin-bottom: 10px;
    }
  }

  &__button {
    display: inline-block;
    padding: 12px 24px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    text-decoration: none;
    transition: all 0.3s ease;

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 5px 20px rgba(102, 126, 234, 0.4);
    }
  }

  &__form {

    .form-group {
      margin-bottom: 20px;

      label {
        display: block;
        font-size: 14px;
        font-weight: 500;
        color: #333;
        margin-bottom: 8px;
      }

      .input-with-icon {
        position: relative;

        input {
          width: 100%;
          padding: 12px;
          border: 1px solid #ddd;
          border-radius: 8px;
          font-size: 14px;
          transition: all 0.3s ease;

          &:focus {
            outline: none;
            border-color: #667eea;
            box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
          }

          &::placeholder {
            color: #999;
          }
        }
      }

      .toggle-visibility {
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translateY(-50%);
        background: none;
        border: none;
        cursor: pointer;
        color: #999;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 4px;
        transition: color 0.3s ease;

        &:hover {
          color: #667eea;
        }

        svg {
          width: 20px;
          height: 20px;
        }
      }

      .error {
        display: block;
        color: #e74c3c;
        font-size: 12px;
        margin-top: 6px;
      }
    }
  }

  &__submit {
    width: 100%;
    padding: 12px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;

    &:hover:not(:disabled) {
      transform: translateY(-2px);
      box-shadow: 0 5px 20px rgba(102, 126, 234, 0.4);
    }

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
  }

  &__footer {
    text-align: center;
    border-top: 1px solid #eee;
    padding-top: 20px;

    p {
      font-size: 13px;
      color: #666;
      margin: 0;
    }
    &-links {
      margin-top: 8px;
      display: flex;
      gap: 12px;
      align-items: center;
      justify-content: space-between;
      flex-wrap: nowrap;
      white-space: nowrap;
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
      padding: 6px 0;

      a.reset-password__link {
        display: inline-flex;
        align-items: center;
        gap: 6px;
        padding: 6px 10px;
        border-radius: 8px;
        color: #667eea;
        text-decoration: none;
        font-weight: 500;
        transition: all 0.16s ease;
        background: transparent;
      }

      a.reset-password__link:hover {
        color: #4f46e5;
        background: rgba(79, 70, 229, 0.06);
        transform: translateY(-1px);
      }
    }
  }

  &__link {
    color: #667eea;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease;

    &:hover {
      color: #764ba2;
    }
  }
}

@media (max-width: 480px) {
  .reset-password {
    padding: 0.5rem;

    &__container {
      padding: 10px;
    }

    &__header {
      margin-bottom: 24px;
    }

    &__logo {
      font-size: 24px;
    }
  }
}
