.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #f8f9fa;

  &__header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    background: #fff;
  }

  &__title {
    font-size: 2rem;
    font-weight: 700;
    color: #1f2937;
    margin: 0 0 0.5rem;
  }

  &__subtitle {
    color: #6b7280;
    font-size: 1rem;
    margin: 0;
    text-align: center;
  }

  &__stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1rem;
    padding: 1rem;
  }

  &__content {
    display: grid;
    /* Make both columns equal width */
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    height: auto;
    padding: 0 1rem 1rem 1rem;
  }

  &__section {
    background: #fff;
    border-radius: 12px;
    padding: 0.5rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  }
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.section-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
}

.btn-link {
  background: none;
  border: none;
  color: #6366f1;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  padding: 0.5rem;

  &:hover {
    text-decoration: underline;
  }
}

.projects-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.project-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  transition: all 0.2s;

  &:hover {
    border-color: #6366f1;
    box-shadow: 0 2px 8px rgba(99, 102, 241, 0.1);
  }

  &__info {
    flex: 1;
  }

  &__name {
    font-size: 1rem;
    font-weight: 600;
    color: #1f2937;
    margin: 0 0 0.5rem;
    /* Truncate long client names */
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__deadline {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.85rem;
    color: #6b7280;
    margin: 0;
    /* Prevent deadline overflowing */
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    svg {
      font-size: 0.9rem;
    }
  }

  &__services {
    margin: 0 0 0.25rem;
    font-size: 0.9rem;
    color: #4b5563;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__status {
    margin-left: 1rem;
  }
}

.status-badge {
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 500;
  white-space: nowrap;
}

.quick-actions {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

.action-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1.5rem 1rem;
  background: #f9fafb;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 0.9rem;
  font-weight: 500;
  color: #374151;

  svg {
    font-size: 1.8rem;
    color: #6366f1;
  }

  &:hover {
    background: #fff;
    border-color: #6366f1;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(99, 102, 241, 0.15);
  }
}

@media (max-width: 1024px) {
  .dashboard {
    &__content {
      grid-template-columns: 1fr;
    }
  }
}

@media (max-width: 768px) {
  .dashboard {
    padding: 1rem;

    &__title {
      font-size: 1.5rem;
    }

    &__stats {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      gap: 1rem;
    }
  }

  .quick-actions {
    grid-template-columns: 1fr;
  }
}

/* Pricing plans added to the dashboard */
.dashboard__plans {
  display: flex;
  gap: 2rem;
  padding: 1rem;
  align-items: stretch;
  flex-wrap: wrap;
}

.plan-card {
  flex: 1 1 320px;
  background: #fff;
  border-radius: 12px;
  padding: 1rem;
  border: 1px solid #e5e7eb;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 260px;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.5rem;
  }

  &__title {
    font-size: 1.05rem;
    font-weight: 700;
    color: #1f2937;
  }

  &__price {
    text-align: right;
  }

  &__amount {
    font-size: 1.25rem;
    font-weight: 700;
    color: #111827;
  }

  &__period {
    font-size: 0.85rem;
    color: #6b7280;
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0.5rem 0 1rem 0;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;

    li {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      color: #374151;
      font-size: 0.95rem;
    }

    .plan-card__check {
      color: #10b981;
      font-size: 0.9rem;
    }
  }

  &__actions {
    display: flex;
    gap: 0.75rem;
    margin-top: 0.5rem;

    button {
      flex: 1;
      padding: 0.65rem 0.75rem;
      border-radius: 8px;
      cursor: pointer;
      font-weight: 600;
    }

    .plan-card__contact {
      text-align: end;
      text-decoration: underline;
      background: none;
      border: none;
      color: #313131;
    }

    .plan-card__activate {
      background: #6366f1;
      border: none;
      color: #fff;
      box-shadow: 0 4px 12px rgba(99, 102, 241, 0.15);
    }
  }

  &--highlight {
    border-color: #fde68a;
    background: linear-gradient(180deg, #ffe88d, #fff);
  }
  /* Silver tier gradient */
  &--silver {
    border-color: #e6e7ea;
    background: linear-gradient(180deg, #9c9c9c 0%, #ffffff 100%);
  }
}

@media (max-width: 768px) {
  .dashboard__plans {
    flex-direction: column;
  }
}
