.register {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 2rem;

  &__container {
    background: #fff;
    border-radius: 16px;
    padding: 3rem;
    max-width: 500px;
    width: 100%;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
    max-height: 90vh;
    overflow-y: auto;
  }

  &__header {
    text-align: center;
    margin-bottom: 2rem;
  }

  &__logo {
    font-size: 2.5rem;
    font-weight: 700;
    color: #6366f1;
    margin: 0 0 0.5rem;
  }

  &__subtitle {
    color: #6b7280;
    font-size: 0.95rem;
    margin: 0;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
  }

  /* Input-with-icon wrapper used for password visibility toggle */
  .input-with-icon {
    position: relative;

    /* Make the input fill the available width and reserve space for the eye button */
    input,
    select {
      display: block;
      width: 100%;
      box-sizing: border-box;
      padding-right: 3.25rem; /* space for the eye button */
    }

    .toggle-visibility {
      position: absolute;
      right: 0.5rem;
      top: 50%;
      transform: translateY(-50%);
      background: transparent;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      color: #6b7280;
      padding: 0.35rem;
      border-radius: 6px;

      &:hover {
        color: #4f46e5;
        background: rgba(79, 70, 229, 0.06);
      }

      svg {
        display: block;
        width: 1.25rem;
        height: 1.25rem;
      }
    }
  }

  &__submit {
    background: #6366f1;
    color: #fff;
    border: none;
    padding: 0.875rem;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
    margin-top: 0.5rem;

    &:hover:not(:disabled) {
      background: #4f46e5;
      transform: translateY(-1px);
      box-shadow: 0 4px 12px rgba(99, 102, 241, 0.4);
    }

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
  }

  &__footer {
    margin-top: 1.5rem;
    text-align: center;
    color: #6b7280;
    font-size: 0.9rem;
  }

  &__link {
    color: #6366f1;
    text-decoration: none;
    font-weight: 500;
    background: none;
    outline: none;
    border: none;
    box-shadow: none;

    &:hover {
      text-decoration: underline;
      cursor: pointer;
    }

    /* Remove default focus box but keep an accessible subtle focus for keyboard users */
    &:focus {
      outline: none;
      box-shadow: none;
    }

    &:focus-visible {
      outline: none;
    }
  }
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;

  label {
    font-size: 0.9rem;
    font-weight: 500;
    color: #374151;
  }

  input,
  select {
    padding: 0.75rem 1rem;
    border: 2px solid #e5e7eb;
    border-radius: 8px;
    font-size: 1rem;
    transition: all 0.2s;

    &:focus {
      outline: none;
      border-color: #6366f1;
      box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
    }
  }

  .error {
    color: #ef4444;
    font-size: 0.85rem;
  }
}

.checkbox {
  display: flex;
  align-items: flex-start;
  gap: 0.5rem;
  cursor: pointer;
  font-size: 0.9rem;
  color: #374151;

  input {
    cursor: pointer;
    margin-top: 0.2rem;
  }
}

@media (max-width: 640px) {
  .register {
    padding: 1rem;

    &__container {
      padding: 2rem 1.5rem;
    }

    &__logo {
      font-size: 2rem;
    }
  }
}
