.login {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 2rem;

  &__container {
    background: #fff;
    border-radius: 16px;
    padding: 2rem;
    max-width: 450px;
    width: 100%;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  }

  &__header {
    text-align: center;
    margin-bottom: 2rem;
  }

  &__logo {
    font-size: 2.5rem;
    font-weight: 700;
    color: #6366f1;
    margin: 0 0 0.5rem;
  }

  &__subtitle {
    color: #6b7280;
    font-size: 0.95rem;
    margin: 0;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }

  /* Input-with-icon wrapper used for password visibility toggle */
  .input-with-icon {
    position: relative;

    /* Make the input fill the available width and reserve space for the eye button */
    input {
      display: block;
      width: 100%;
      box-sizing: border-box;
      padding-right: 3.25rem; /* space for the eye button */
    }

    .toggle-visibility {
      position: absolute;
      right: 0.5rem;
      top: 50%;
      transform: translateY(-50%);
      background: transparent;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      color: #6b7280;
      padding: 0.35rem;
      border-radius: 6px;

      &:hover {
        color: #4f46e5;
        background: rgba(79, 70, 229, 0.06);
      }

      svg {
        display: block;
        width: 1.25rem;
        height: 1.25rem;
      }
    }
  }

  &__options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -0.5rem;
  }

  &__submit {
    background: #6366f1;
    color: #fff;
    border: none;
    padding: 0.875rem;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;

    &:hover:not(:disabled) {
      background: #4f46e5;
      transform: translateY(-1px);
      box-shadow: 0 4px 12px rgba(99, 102, 241, 0.4);
    }

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
  }

  &__forgot-below {
    text-align: center;
    margin-top: 0.5rem;
    .login__link {
      display: inline-block;
    }
  }

  /* New footer row for Reset / Forgot / Sign Up links */
  &__footer-row {
    margin-top: 1.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    color: #6b7280;
    font-size: 0.95rem;
    flex-wrap: nowrap;
    white-space: nowrap;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    padding: 6px 0;

    .login__link {
      color: #667eea;
      text-decoration: none;
      font-weight: 500;
      display: inline-flex;
      align-items: center;
      gap: 6px;
      padding: 6px 10px;
      border-radius: 8px;
      transition: all 0.16s ease;
    }

    .login__link:hover {
      color: #4f46e5;
      background: rgba(79, 70, 229, 0.06);
      transform: translateY(-1px);
    }
  }

  &__footer {
    text-align: center;
    border-top: 1px solid #eee;
    padding-top: 20px;

    p {
      font-size: 13px;
      color: #666;
      margin: 0;
    }

    &-links {
      margin-top: 8px;
      display: flex;
      gap: 12px;
      align-items: center;
      justify-content: space-between;
      flex-wrap: nowrap;
      white-space: nowrap;
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
      padding: 6px 0;
    }
  }

  &__link {
    color: #667eea;
    text-decoration: none;
    font-weight: 500;

    transition: color 0.3s ease;

    &:hover {
      color: #764ba2;
    }
  }
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;

  label {
    font-size: 0.9rem;
    font-weight: 500;
    color: #374151;
  }

  input {
    padding: 0.75rem 1rem;
    border: 2px solid #e5e7eb;
    border-radius: 8px;
    font-size: 1rem;
    transition: all 0.2s;

    &:focus {
      outline: none;
      border-color: #6366f1;
      box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
    }
  }

  .error {
    color: #ef4444;
    font-size: 0.85rem;
  }
}

.checkbox {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  font-size: 0.9rem;
  color: #374151;

  input {
    cursor: pointer;
  }
}

@media (max-width: 640px) {
  .login {
    padding: 0.5rem;

    &__container {
      padding: 1rem;
    }

    &__logo {
      font-size: 2rem;
    }
    &__footer-row {
      gap: 0.75rem;
    }
  }
}
