.terms-modal {
  position: fixed;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1200;

  &__backdrop {
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.5);
  }

  &__panel {
    position: relative;
    width: min(920px, 96%);
    max-height: 86vh;
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 20px 40px rgba(2, 6, 23, 0.4);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    z-index: 2;
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1.25rem;
    border-bottom: 1px solid #eef2ff;
    background: #f8fafc;

    h2 {
      margin: 0;
      font-size: 1.125rem;
      color: #111827;
    }
  }

  &__close {
    background: transparent;
    border: none;
    font-size: 1.125rem;
    cursor: pointer;
    color: #374151;
  }

  &__content {
    padding: 1rem 1.25rem;
    overflow: auto;
    line-height: 1.55;
    color: #374151;

    h3 {
      margin-top: 1rem;
      margin-bottom: 0.5rem;
      color: #111827;
    }

    p {
      margin: 0 0 0.75rem 0;
    }
  }

  &__contact {
    padding: 1rem 1.25rem;
    border-top: 1px solid #eef2ff;
    background: #fbfdff;

    h3 {
      margin: 0 0 0.5rem 0;
      font-size: 1rem;
      color: #111827;
    }

    p {
      margin: 0 0 0.5rem 0;
      color: #374151;
    }

    ul {
      margin: 0;
      padding: 0;
      list-style: none;

      li {
        margin: 0 0 0.4rem 0;
        color: #374151;

        a {
          color: #4f46e5;
          text-decoration: none;
        }
      }
    }
  }

  &__footer {
    padding: 0.75rem 1.25rem;
    display: flex;
    justify-content: flex-end;
    gap: 0.5rem;
    border-top: 1px solid #eef2ff;
    background: #fff;
  }

  &__accept {
    background: #6366f1;
    color: #fff;
    border: none;
    padding: 0.5rem 0.85rem;
    border-radius: 8px;
    cursor: pointer;
  }
}

@media (max-width: 640px) {
  .terms-modal__panel {
    width: 96%;
    border-radius: 8px;
  }
}
