.sidebar {
  background: #1f2937;
  height: calc(100vh - 50px);
  position: sticky;
  top: 60px;
  transition: width 0.3s ease;
  overflow: hidden;
  display: flex;
  flex-direction: column;

  &--open {
    width: 170px;
  }

  &--closed {
    width: 80px;
  }

  &__nav {
    padding: 0.5rem 0;
    flex: 1 1 auto; /* allow nav to take available space */
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1rem 1.5rem;
    color: #d1d5db;
    text-decoration: none;
    transition: all 0.2s;
    position: relative;
    white-space: nowrap;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 4px;
      background: #6366f1;
      transform: scaleY(0);
      transition: transform 0.2s;
    }

    &:hover {
      background: rgba(99, 102, 241, 0.1);
      color: #fff;
    }

    &--active {
      background: rgba(99, 102, 241, 0.15);
      color: #fff;

      &::before {
        transform: scaleY(1);
      }

      .sidebar__icon {
        color: #6366f1;
      }
    }
  }

  &__icon {
    font-size: 1.5rem;
    min-width: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__label {
    font-size: 0.95rem;
    font-weight: 500;
  }

  /* Logout button specific tweaks */
  &__logout {
    width: calc(100% - 1rem);
    margin: 2rem auto;
    justify-content: flex-start;
    gap: 0.75rem;
    background: none;
    border: none;
    cursor: pointer;
    color: #d1d5db;
    padding: 0.8rem 1rem;
    border-radius: 6px;

    &:hover {
      background: rgba(99, 102, 241, 0.1);
      color: #fff;
    }
    /* Rotate the logout icon so it doesn't resemble the login icon */
    .sidebar__icon {
      transform: rotate(180deg);
      /* ensure smoothness if other transforms are added */
      transition: transform 0.2s;
    }
  }
}

@media (max-width: 768px) {
  .sidebar {
    position: fixed;
    left: 0;
    z-index: 99;
    transform: translateX(-100%);

    &--open {
      transform: translateX(0);
      width: 170px;
    }

    &--closed {
      transform: translateX(-100%);
    }
  }
}

/* Laptop screens: reduce width and spacing so sidebar feels slimmer on laptops */
@media (min-width: 769px) and (max-width: 1440px) {
  .sidebar {
    &--open {
      width: 140px; /* slightly narrower than desktop */
    }

    &--closed {
      width: 64px; /* a bit narrower when collapsed */
    }

    &__item {
      padding: 0.75rem 1rem;

      &__icon,
      .sidebar__icon {
        font-size: 1.25rem;
        min-width: 20px;
      }

      .sidebar__label {
        font-size: 0.9rem;
      }
    }
  }
}
